<template>
  <div class="data-panel def-shadow">

    <div class="row ">
      <div class="col-sm-12">
        <div class="table-header h4 d-inline float-left text-accent">全部</div>
      </div>
    </div>

    <div class="row">
      <div class="col-sm-12">

        <table class="table data-table br-t-none">
          <thead class="br-t-none">
            <tr class="br-t-none">
              <th class="text-center">编号</th>
              <th >名称</th>
              <th >地址</th>
              <th class="text-center">联络负责人</th>
              <th class="text-center">负责人电话</th>
              <th >操作</th>
            </tr>
          </thead>
          <tbody>
            <tr v-for="f in members" :key="f.id">
              <th scope="row" class="text-center">{{ f.num }}</th>
              <td>{{ f.named }}</td>
              <td>{{ f.addr }}</td>

              <td class="text-center">
                {{ f.contact_named }}
              </td>
              <td class="text-center">
                {{ f.contact_phoned }}
              </td>
              
              <td>
                <i @click="option(f.id, 'edit')">
                  <pencil></pencil>
                </i>
                <i @click="option(f.id, 'trash')">
                  <remove></remove>
                </i>
              </td>
            </tr>
          </tbody>
        </table>

      </div>
    </div>
    
  </div>
</template>

<script>
import service from '@/service'
import Pencil from '@/components/Option/Pencil.vue'
import Remove from '@/components/Option/Remove.vue'

export default {
  name: 'MemberViewDataTable',
  components: { Pencil, Remove },
  data() {
    return {
      members: service.members
    }
  },
  methods: {
    option(id, option) {
      if (option == 'edit') {
        alert(`编辑 ID = ${id} 的客户。`)
      }
      else if (option == 'trash') {
        // tool.showModal('freightTrashModal')
        alert(`删除 ID = ${id} 的客户。`)
      }
    }
  }
}
</script>

<style lang="sass" scoped>
@import "@/common/sass/base.sass"

</style>


